class GenerateChartData 
{
    /**
     * @param {Array} data data from database
     * 
     * example use:
     * let generator = new GenerateChartData(data);
     * 
     * generator->add("totals->orders->sales", "valor");
     * generator->add("totals->orders->quantity", "ventas");
     * generator->add("totals->total_forms", "formularios");
     * 
     * let chart_data = generator->render();
     * 
     */
    constructor(data)
    {        
        if(!Array.isArray(Object.values(data))){
            console.error("argument #1 GenerateChartData constructor is not an array.")
            return false;
        }
        this.data = data;
        this.labels = [];
        this.series = [];

        this.#set_labels();
    }
    #set_labels()
    {
        let app = this;
        this.labels = [];
        Object.keys(app.data).map(( name )=>{
            app.labels.push(".");
        })
    }
    #set_series(setter, metaname)
    {
        let app = this;

        
        let series_container = [];
        Object.values(app.data).map(( item, key )=>{
            let label = Object.keys(app.data)[key];
            let render_field = item;
            let data_saved = [];
            setter.map(( field )=>{
                render_field = render_field[field];                
            })
            if(typeof render_field !== "undefined"){
                data_saved.push(render_field)
            }

            data_saved.map(( value )=>{
                series_container.push({
                    meta: label,
                    value: +(value),
                })
            })
        })

        this.series.push(series_container);
    }
    /**
     * @param {String} setter How to access data, for example: "totals->total_orders"
     * @param {String} metaname name of the meta name for values.
     */
     add(setter, metaname)
     {
        setter = setter.split("->");
        metaname = metaname || "Valor";        
        this.#set_series(setter, metaname);
     }
     render()
     {
        return {
            labels: this.labels,
            series: this.series,
        }
     }
}



export {GenerateChartData}