<script>
import ChartistGraphic from './../../components/charts/ChartistGraphic.svelte';
import IndexHeaderBox from './IndexHeaderBox.svelte';
import { ApplicationData } from '../../data.application.route';
import { CustomStore } from '../../libraries/customStore/CustomStore';
import LinkPrimary from './../../components/Buttons/LinkPrimary.svelte';
import Graphic from '../../components/charts/Graphic.svelte';
let isData = true;

let dashboardData = new CustomStore(
    {
        order_counter: 0,
        order_value: 0,
        form_counter: 0,
        form_value: 0,
        from: "",
        until: "",
        orders: {
            completeData: {},
            labels: [],
            data: []
        },
        forms: {
            completeData: {},
            labels: [],
            data: []
        }
    },
    function(){
        dashboardData = dashboardData;
    }
);

// paginatorData.update((object)=>{                
//                 object.conditions.offset = offset;
//                 return object; 
//             })

let show_charts = false;
let data_charts = new CustomStore({}, ()=>{
    data_charts = data_charts;
})

function fetch_chart(){
    let url = `${$ApplicationData.api_host}public/api/v1/services/user/dashboard_chart`;

    if(dashboardData.data.from !== "" && dashboardData.data.until !== ""){
        url = url+`?from=${dashboardData.data.from}&until=${dashboardData.data.until}`
    }

    fetch(url, {
        headers: {'X-Token-Website': $ApplicationData.token_website},
    }).then(r => r.json()).then((r)=>{
        data_charts.update(function(data){
            data = r
            return data;
        })
        show_charts = true;
    })
}

function dashboard_fetch_all() {
    
    
    fetch_chart();

    fetch(`${$ApplicationData.api_host}public/api/v1/services/builder/orders/dashboard?search=&from=${dashboardData.data.from}&until=${dashboardData.data.until}`, {
        headers: {'X-Token-Website': $ApplicationData.token_website},
    }).then(r => r.json()).then((r)=>{    
        console.log(r)
        dashboardData.setJson("order_value", r.sum);
        dashboardData.setJson("order_counter", r.count);
        dashboardData.update((object)=>{
            object.orders.completeData = r.data;
            return object;
        })
    })
}

dashboard_fetch_all();




</script>
<style>
.info-container{
    width: 100%;
    margin-bottom: 2rem;
    height: fit-content;
    font-size: 14px;
    gap: 1rem;
}
.button{
    background: #3DCC14;
    border: none;
    color: white;
    font-size: 14px;
    border-radius: 46px;
}
.form-sales-container{
    width: 30%;
    height: 100%;
}
.form-sales-container .card{
    background: var(--color-grey-background);
    border: 2px solid #FFFFFF45;
    border-radius: 15px;
    height: 100%;
    width: 100%;
    padding: 0.75rem;
    color: #415B76;
    font-size: 14px;
}
.form-sales-container .card span{
    font-size: 18px;
}
.form-sales-container .card .is-line{
    width: 100%;
}
.info-container .graphics-container{
    margin-left: 1rem;
    width: 100%;
    height: 100%;
    gap: 1rem;
}
.sales-chart, .forms-chart{
    width: 100%;
    height: 100%;
}

@media only screen and (max-width: 1500px){
    .info-container{
        flex-direction: column;
        height: fit-content;
        align-items: center;
    }
    .info-container .form-sales-container{
        width: 60%;
        padding: 1rem;
        flex-direction: row;
    }
    .sales-chart, .forms-chart{
        width: 100%;
    }
    .info-container .graphics-container{
        padding: 1rem;
        margin-left: 0;
    }
}

@media only screen and (max-width: 1100px){
    .info-container .graphics-container{
        width: 80%;
        min-height: 250px;
        flex-direction: column;
        align-items: center;
    }
        
    .info-container .form-sales-container{
        width: 80%;
    }
}

@media only screen and (max-width: 900px){
    .info-container .graphics-container{
        width: 100%;
    }
    .info-container .form-sales-container{
        width: 100%;
    }
}

@media only screen and (max-width: 600px){
    .info-container .form-sales-container{
        padding: 1rem 0;
    }
    .info-container .graphics-container{
        padding: 1rem 0;
    }
}

</style>
{#if $ApplicationData.has_projects === true}
    <section>
        <IndexHeaderBox dashboardData={dashboardData} dashboard_fetch_all={dashboard_fetch_all}></IndexHeaderBox>
        <div class="info-container mt-3 flex-start-space-around">
            <div class="form-sales-container flex-column">
                <LinkPrimary type="linkContainer" path="projects/orders">
                    <div class="card flex-column-space-between">
                        <div class="is-line flex-center-space-between">
                            <span class="has-text-weight-medium">Ventas</span> <i class="icon-tripod-shopping-cart"></i>
                        </div>
                        <div class="is-line flex-center-space-between">
                            <p>Ordenes: <span class="has-text-weight-medium">{dashboardData?.data?.order_counter}</span></p>
                        </div>
                        <div class="is-line flex-center-space-between">
                            <p>valor: <span class="has-text-weight-medium">{`$${dashboardData?.data?.order_value}`}</span></p>
                        </div>
                        <div class="is-line flex-center-flex-end">
                            <p style="font-size: 12px; color: #296fff;">Ver más</p>
                        </div>
                    </div>
                </LinkPrimary>
                <!-- <div class="is-line" style="padding: 0.5rem;"></div> -->
                <!-- <LinkPrimary type="linkContainer" path="projects/forms">
                    <div class="card flex-column-space-between">
                        <div class="is-line flex-center-space-between">
                            <span class="has-text-weight-medium">Formularios</span> <i class="icon-tripod-list-v2"></i>
                        </div>
                        <div class="is-line flex-center-space-between">
                            <p>Ordenes: <span class="has-text-weight-medium">{dashboardData?.data?.form_counter}</span></p>
                        </div>
                        <div class="is-line flex-center-space-between">
                            <p>valor: <span class="has-text-weight-medium">{`$${dashboardData?.data?.form_value} COP`}</span></p>
                        </div>
                        <div class="is-line flex-center-flex-end">
                            <p style="font-size: 12px; color: #296fff;">ver más</p>
                        </div>
                    </div>
                </LinkPrimary> -->
            </div>
            <div class="graphics-container flex-center">
                {#if show_charts}
                    <div class="sales-chart">
                        <ChartistGraphic data_charts={data_charts} type="sales-value" chartId="chart1" chartTittle="Ingresos"></ChartistGraphic>
                    </div>
                    <div class="forms-chart">
                        <ChartistGraphic data_charts={data_charts} type="sales-counter" chartId="chart2" chartTittle="Numero de ventas"></ChartistGraphic>
                    </div>
                {/if}
                
            </div>
        </div>
        <div class="line flex-center">
            <button class="button brand" on:click="{function(e){ $ApplicationData.go_to_project() }}" style="padding: 24px 31px;">
                Gestionar proyectos
            </button>
        </div>
    </section>
{/if}
