<script>
import LoaderElement from './../../../../components/Elements/LoaderElement.svelte';
import Table from "../../../../components/Table/Table.svelte";
import { AppDate } from "../../../../libraries/coreApp";
import { ApplicationData } from '../../../../data.application.route';
import { ChecksSelectedData } from '../../../../data.checks_selected.js';

export let paginatorData;

let checkList = [];
let countries = [];
let checkPermision = true;


$:{
    if(paginatorData?.data){
        console.log("gbfjds");
        checkList = $ChecksSelectedData.checks_selected;
        paginatorData?.data?.listData?.data.map((item) => {
            if($ChecksSelectedData.checks_selected.includes(item.invoice_no)){
                return item.isChecked = true;
            }
            else{
                return item.isChecked = false;
            }
        });
        paginatorData = paginatorData;
        console.log(paginatorData.data);
    }
}

function checkboxActive(id) {
    console.log(checkPermision);
    let element = document.getElementById(`${id}`);

    if($ChecksSelectedData.checks_selected.length < paginatorData.data.checksLimit){
    
        checkList = $ChecksSelectedData.checks_selected;
    
        if(element.checked){
            checkList = [...checkList, element.id];
        }
        else{
            let checkExists = checkList.filter((check) => check !== element.id);
            checkList = checkExists;
        }
    
        paginatorData.update((object) => {
            object.checks_selected = checkList;
            return object;
        })
    
        console.log(checkList);
    
        ChecksSelectedData.update("checks_selected", checkList);
    }
    else{
        checkPermision = false;
        alert("No puedes seleccionar mas de 4");
      
        element.checked = false;
    }
}

function updateAllChecks(data) {
    checkList = data;

    paginatorData.update((object) => {
        object.checks_selected = checkList;
        return object;
    })

    console.log(checkList);

    ChecksSelectedData.update("checks_selected", checkList);

    console.log($ChecksSelectedData.checks_selected);
}

function get_countries(){
    let countriesCode = paginatorData?.data?.listData?.data?.map(( item ) => {
        return item.shipping_address.country;
    });

    let countriesCodeFromApi = $ApplicationData.countries?.filter(( item ) => {
        return countriesCode.includes(item.c2);
    });

    countriesCode.map((item) => {
        countriesCodeFromApi.map((item2) => {
            if(item === item2.c2){
                countries.push(item2.name);
            }
        })
    })
}

get_countries();

</script>
<style>
.w-100{
    width: 100%;
}
.list-item-table{
    font-size:0.90rem;
    text-align: left;
    color: var(--color-grey);
    font-weight: 500;
} 
.link-item{
    font-size: 1rem;
    font-weight: 500;
    color: #296FFF;
}

</style>
<Table listHeader={['Checkbox', 'Orden','Fecha','Destinatario','Ubicación','Total', 'Estado', 'Aplicaciones']} paginatorData={paginatorData} updateAllChecks={updateAllChecks} >
    {#each paginatorData?.data?.listData?.data as data, i}
        <tr class="w-100">
            <td class="list-item-table">
                <input type="checkbox" class="input-checkbox" id={data.invoice_no} disabled={data.status === "pending" ? false : true} checked={data?.isChecked ? true : false}  on:click={() => checkboxActive(data.invoice_no)}>
            </td>
            <td class="list-item-table">{data.invoice_no}</td>
            <td class="list-item-table">{AppDate.onlydate(data.created_at, "date")}</td>
            <td class="list-item-table">
                <p style="overflow-wrap: anywhere;">
                    {data.customer_name}<br>
                    {data.customer_email}
                </p>
            </td>
            <td class="list-item-table">
                {countries[i]}
            </td>
            <td class="list-item-table">${data.total}</td>
            <td class="list-item-table" style="color: {data.statusHanddler?.color};"><div class="flex-center-flex-start"><i class="{data.statusHanddler?.icon}"></i> <p style="white-space: nowrap;">{data.statusHanddler?.text}</p></div></td>
            <td class="list-item-table">
                <a href="#path:/projects/orders/{data.invoice_no}">
                    Ver más detalles
                </a>
            </td>
        </tr>   
    {/each}
</Table>